@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-allies {
  position: relative;
  min-height: 100vh;
  text-align: center;
  background-color: black;
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  background-position: 5% 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  .gradient {
    background-image: linear-gradient(#081d21, #14373cc9, #1c586242, #1c586200);
  }
  @include mobile {
    .bg-image {
      object-fit: unset;
    }
  }
  @include tablet {
    min-height: unset;
  }
  @include desktop {
    padding: $space-m;
    background-position: unset;
  }

  &__title {
    position: relative;
    @include title;

    @include desktop {
      padding: $space-s;
    }
  }

  &__description {
    position: relative;
    @include description;
    padding: 0 $space-m;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;

    @include desktop {
      flex-direction: row;
    }
  }

  &__card {
    position: relative;
    padding: 0 $space-m;
    padding-top: $space-m;

    @include desktop-l {
      padding: 0 $space-l;
    }

    img {
      width: 100%;
      height: 75vh;
      object-fit: contain;
      object-position: top;

      @include desktop {
        height: 30vh;
      }

      @include desktop-l {
        height: 50vh;
      }
      @include laptop {
        height: 48vh;
      }
      &.bg-mobile {
        @include desktop {
          display: none;
        }
      }

      &.bg-desk {
        display: none;
        @include desktop {
          display: block;
        }
      }
    }
  }

  &__card--content {
    position: absolute;
    top: 5%;
    left: $space-xxl;
    right: $space-xxl;
    line-height: 1.5;

    @include desktop {
      display: none;
      width: 55%;
      top: 20%;
      left: $space-m;
      right: unset;
      font-weight: 500;
      line-height: 1.15rem;
    }

    .title {
      font-size: 1.5rem;
      font-weight: 700;
      text-transform: uppercase;
      margin: 0.5rem 0;
      color: $color-p-yellow;
      @include desktop {
        text-align: left;
        margin-top: 0;
        margin-bottom: 0.75rem;
      }
    }

    .content {
      color: white;
      font-size: 0.95rem;

      @include tablet {
        max-width: 60%;
        line-height: 1.75;
        font-size: 1.15rem;
        margin: auto;
      }

      @include desktop {
        text-align: left;
        font-size: 0.85rem;
        line-height: 1rem;
      }
    }
  }
}

.carousel-allies {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  position: relative;
  padding-bottom: $space-m;

  .carousel {
    &__wrapper {
      overflow: hidden;
      width: 100%;
    }

    &__container {
      display: flex;
      will-change: transform;
      @include desktop {
        padding: 2rem 0;
      }
    }

    &__slide-container {
      position: relative;
      flex: 0 0 auto;
      width: 100%;
      transition: 0.3s all ease;
      @include desktop-l {
        width: 50%;
      }

      &.is-selected {
        @include desktop {
          &:nth-child(odd) {
            transform: translateY(-5vh);
            .com-allies__card--content {
              right: $space-s;
              left: unset;
            }
          }
          &:nth-child(even) {
            transform: translateY(5vh);
          }
        }
        @include laptop {
          &:nth-child(even) {
            transform: scale(1.1) translateY(-3vh);
          }
        }
      }
    }
  }

  .carousel-draggable {
    cursor: grab;
  }

  .carousel-dragging {
    cursor: grabbing;
  }

  .carousel-dots-container {
    .carousel-dot {
      cursor: pointer;
      width: 12px;
      height: 12px;
      background: transparent;
      border: 2px solid $color-p-yellow;
      border-radius: 50%;
      margin: 4px;

      &.is-selected {
        background: $color-p-yellow;
      }
    }
  }

  .carousel-buttons-container {
    .carousel-button {
      position: absolute;
      top: 40%;
      width: 50px;
      height: 50px;
      background: transparent;
      border: 0;
      outline: none;
      cursor: pointer;
      filter: brightness(300%);
      transition: 0.3s all ease;
      transform: scale(0.65);
      @include desktop {
        transform: scale(1);
      }
      &:first-child {
        left: 0;
      }

      &:last-child {
        right: 0;
      }
    }

    .is-disable {
      filter: brightness(70%);
      cursor: not-allowed;
    }
  }
}
