@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-legal {
  &__content {
    padding: calc(var(--space-l) + 90px) $space-c-x $space-c-b $space-c-x;
    background-image: linear-gradient(#081d21, #14373cc9, #1c586242, #1c586200), url("~assets/images/allies/bg-tree.png");
    background-color: black;
    background-size: cover;
    background-position: top;
    min-height: 100vh;
    // max-width: 1920px;
    color: $color-p-white;
  }

  &__title {
    @include title;
    padding-bottom: $space-m;
  }

  &__description {
    @include description;
    p {
      padding-bottom: $space-s;
    }

    li {
      list-style: disc;
      padding-bottom: $space-xs;
    }
  }
}
