@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-footer {
  background-color: $bg-footer;
  color: white;

  &__image {
    display: flex;
    align-items: center;
    padding: $space-xxl $space-m;
    user-select: none;

    @include desktop {
      max-width: 40vw;
      padding: $space-m 0;
      margin: auto;
    }
  }

  &__image--logo {
    flex: 2;

    img {
      cursor: pointer;
      width: 80%;
      @include tablet {
        width: 60%;
      }
      @include desktop {
        max-width: 230px;
      }
    }
  }

  &__image--link {
    flex: 1;
    text-align: right;

    img {
      height: 40px;
      cursor: pointer;
    }
  }

  ul {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include desktop {
      flex-direction: row;
      justify-content: center;
    }

    li {
      font-size: 1.15rem;
      line-height: 2;
      width: fit-content;
      user-select: none;

      @include desktop {
        font-size: 1rem;
        font-weight: 700;
        margin-right: 1.5rem;
        position: relative;

        &:last-child::after {
          display: none;
        }

        &::after {
          content: " ";
          position: absolute;
          right: -0.75rem;
          width: 4px;
          height: 50%;
          top: 25%;
          background-color: white;
        }
      }
    }
  }

  p {
    text-align: center;
    line-height: 2;
    user-select: none;

    &:last-child {
      padding-bottom: $space-m;
    }

    a {
      text-decoration: underline;
      margin: 0 0.25rem;
    }

    @include desktop {
      font-size: 0.9rem;
    }
  }
}
