@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-about {
  position: relative;
  display: flex;
  min-height: 100vh;
  color: $color-p-white;

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(#000000, 1) 5%, rgba(#ffffff, 0));
    z-index: 0;
    @include tablet {
      background: linear-gradient(180deg, rgba(#16211d, 0) 50%, rgba(#16211d, 0.42) 75%, rgba(#16211d, 0.79) 95%, rgba(#16211d, 1));
    }
  }

  &__content {
    text-align: center;
    margin: auto;
    z-index: 1;
    max-width: 80%;
  }

  &__title {
    @include title;
  }

  &__description {
    @include description;
    margin: 0 auto;
    p {
      padding-bottom: $space-m;
      &:last-child {
        font-weight: 600;
        text-transform: uppercase;
      }
    }
    @include tablet {
      max-width: 80%;
    }
  }
}
