@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-slider-desk {
  .carousel {
    &__wrapper {
      overflow: hidden;
      width: 100%;
    }

    &__container {
      display: flex;
      will-change: transform;
      @include desktop {
        padding: 2rem 0;
      }
    }

    &__slide-container {
      position: relative;
      flex: 0 0 auto;
      width: 100%;
      transition: 0.3s all ease;
      @include desktop-l {
        width: 50%;
      }

      &.is-selected {
        @include desktop {
          &:nth-child(odd) {
            transform: translateY(-5vh);
            .com-allies__card--content {
              right: $space-s;
              left: unset;
            }
          }
          &:nth-child(even) {
            transform: translateY(5vh);
          }
        }
        @include laptop {
          &:nth-child(even) {
            transform: scale(1.1) translateY(-3vh);
          }
        }
      }
    }
  }
  .carousel-draggable {
    cursor: grab;
  }

  .carousel-dragging {
    cursor: grabbing;
  }

  .carousel-dots-container {
    .carousel-dot {
      cursor: pointer;
      width: 12px;
      height: 12px;
      background: transparent;
      border: 2px solid $color-p-yellow;
      border-radius: 50%;
      margin: 4px;

      &.is-selected {
        background: $color-p-yellow;
      }
    }
  }

  .carousel-buttons-container {
    .carousel-button {
      position: absolute;
      top: 40%;
      width: 50px;
      height: 50px;
      background: transparent;
      border: 0;
      outline: none;
      cursor: pointer;
      filter: brightness(300%);
      transition: 0.3s all ease;
      transform: scale(0.65);
      @include desktop {
        transform: scale(1);
      }
      &:first-child {
        left: 0;
      }

      &:last-child {
        right: 0;
      }
    }

    .is-disable {
      filter: brightness(70%);
      cursor: not-allowed;
    }
  }
}
