@mixin mobile-l {
  @media (min-width: 599px) and (max-height: 1024px) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: 619px) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: 620px) {
    @content;
  }
}

@mixin tablet-l {
  @media (min-width: 1024px) and (min-height: 1366px) {
    @content;
  }
}

@mixin laptop-s {
  @media (min-width: 1024px) and (max-height: 680px) {
    @content;
  }
}

@mixin laptop {
  @media (min-width: 1024px) and (max-height: 768px) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin desktop-l {
  @media (min-width: 1280px) {
    @content;
  }
}

@mixin desktop-xl {
  @media (min-width: 1900px) {
    @content;
  }
}

@mixin desktop-2k {
  @media (min-width: 2348px) {
    @content;
  }
}

@mixin desktop-4k {
  @media (min-width: 3500px) {
    @content;
  }
}

@mixin desktop-8k {
  @media (min-width: 7380px) {
    @content;
  }
}

@mixin title {
  font-weight: 800;
  text-transform: uppercase;
  user-select: none;
  font-size: 2.2rem;
  color: $color-p-yellow;
  padding: $space-m;
  text-align: center;
  @include desktop {
    font-size: 4rem;
  }
  @include laptop() {
    font-size: 3.5rem;
  }
}

@mixin description {
  user-select: none;
  font-size: 1.35rem;
  font-weight: 500;
  line-height: 1.5rem;
  color: white;
  padding: 0;
  margin-bottom: $space-m;
  @include desktop {
    padding: 0 $space-l;
    font-size: 1.4rem;
  }
  @include laptop() {
    font-size: 1.3rem;
  }
}
