@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-news {
  position: relative;
  padding: $space-l $space-c-x $space-c-b $space-c-x;
  margin: 0 auto;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  @include tablet {
    padding: $space-c-t $space-m $space-c-b $space-m;
  }

  &__title {
    @include title;
    min-width: 216px;
    margin-bottom: $space-m;
  }

  &__description {
    @include description;
    color: $color-p-white;
    text-align: center;
    margin-bottom: $space-m;
    max-width: 600px;
  }

  &__follow-us {
    font-size: 1rem;
    padding: 1rem 1.2rem;
    margin-bottom: $space-m;
  }

  &__feed-container {
    display: flex;
    min-height: 1000px;
  }

  &__column {
    padding: $space-s $space-s 0;
  }

  &__see-more {
    position: absolute;
    bottom: $space-m;
    font-size: 1rem;
    padding: 1rem 1.2rem;
    z-index: 4;
  }

  &__loading {
    position: absolute;
    bottom: $space-m;
    z-index: 4;
  }

  &__overlay {
    position: absolute;
    pointer-events: none;
    width: 100%;
    height: 600px;
    bottom: 0;
    left: 0;
    z-index: 4;
    background: linear-gradient(180deg, rgba(#5a4c1e, 0) 0%, rgba(#312a12, 1) 55%, rgba(#080704, 1));
  }
}
