@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-signup {
  position: relative;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  padding: $space-xxl $space-c-x $space-c-b $space-c-x;
  z-index: 0;
  color: $color-p-white;

  @include desktop {
    padding: $space-xxl 0 0;
  }

  &__join-sign {
    width: 209px;
    z-index: 1;
    transition: width 0.2s ease;
    @include desktop {
      width: 389px;
    }
  }

  &__character {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-136%, -49%);
    width: 65%;
    max-width: fit-content;
    z-index: 1;
    pointer-events: none;
    @include laptop {
      width: 56%;
      transform: translate(-146%, -45%);
    }
  }

  &__content {
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    background-color: #505050;
    padding: $space-xxl $space-l $space-l;
    margin-top: -42px;
    text-align: center;
    align-items: center;
    max-width: 400px;
    width: 90%;
    box-shadow: 0px 3px 3px rgba($color: #000000, $alpha: 0.55);
    @include desktop {
      margin-top: -52px;
      max-width: 800px;
      width: 70%;
      grid-template-columns: 1fr 1fr;
      column-gap: $space-s;
      padding: $space-l $space-xxl;
      margin-bottom: 10rem;
      background: none;
      box-shadow: none;
    }
    @include laptop {
      padding-top: $space-xl;
    }
  }

  &__panel {
    position: absolute;
    top: 0;
    left: 50%;
    min-width: 800px;
    transform: translateX(-50%);
    z-index: -1;
    filter: drop-shadow(0 3px 3px rgba($color: #000000, $alpha: 0.55));
  }

  &__description {
    padding-bottom: $space-m;
    font-size: 0.9rem;
    @include laptop {
      font-size: 1.25rem;
    }
  }

  &__input-container {
    width: 100%;
    margin-bottom: $space-s;
  }

  &--span-column {
    @include desktop {
      grid-column: 1/3;
    }
  }

  &__input,
  &__select {
    appearance: none;
    border: none;
    user-select: none;
    outline: none;
    width: 100%;
    height: 2.3rem;
    padding-left: 1.2rem;
    color: #21384e;
    font-size: 0.8rem;
    font-weight: 500;
    &::placeholder {
      color: #21384e;
      font-style: italic;
    }
  }

  &__select:invalid {
    color: #21384e;
    font-style: italic;
  }

  &--add-strip {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      height: 0.2rem;
      width: 100%;
      background-color: $color-s-yellow;
      @include desktop {
        height: 0.4rem;
      }
    }
  }

  &--add-arrow {
    &:before {
      content: "";
      position: absolute;
      pointer-events: none;
      top: 50%;
      right: 10px;
      transform: translateY(-50%) rotate(-45deg);
      width: 8px;
      height: 8px;
      border-left: 2px solid #444;
      border-bottom: 2px solid #444;
      border-radius: 1px;
      z-index: 1;
    }
  }

  &__legal {
    font-size: 0.8rem;
    margin: $space-l 0;
    text-align: left;
    @include desktop {
      margin: $space-xs 0;
    }
    @include laptop {
      font-size: 1rem;
    }
  }

  &__label {
    font-size: 0.8rem;
    display: flex;
    align-items: flex-start;
    margin: 0 auto $space-m 0;
    text-align: left;
    cursor: pointer;
    @include desktop {
      margin: 0 auto $space-s 0;
    }
    @include laptop {
      font-size: 1rem;
    }
  }

  &__checkbox {
    position: relative;
    appearance: none;
    user-select: none;
    outline: none;
    cursor: pointer;
    background-color: $color-p-white;
    padding: 0.4rem;
    border: 2px solid $color-p-yellow;
    border-radius: 3px;
    margin: 0.2rem 1rem 0 0;
    &:checked:after {
      position: absolute;
      content: "\2714";
      font-size: 2em;
      top: 25%;
      left: 65%;
      transform: translate(-50%, -50%);
      color: $color-p-yellow;
    }
  }

  &__loading {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 50%);
    background-size: cover;
    padding: 1.95rem 3.85rem;
    img {
      position: absolute;
      width: 50px;
      bottom: 5px;
      left: 50%;
      transform: translate(-50%);
      @include desktop {
        width: 80px;
        bottom: 13px;
      }
      @include laptop {
        bottom: 0;
      }
    }

    @include desktop {
      transform: translate(-50%, 70%);
      padding: 2.5rem 5.2rem;
    }
    @include laptop {
      transform: translate(-50%, 190%);
      padding-bottom: 2.26rem;
    }
  }

  &__submit {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 50%);
    appearance: none;
    border: none;
    font-weight: bold;
    background: url("~assets/images/signup/submit-bg.png") no-repeat;
    background-size: contain;
    cursor: pointer;
    outline: none;
    @include desktop {
      transform: translate(-50%, 70%);
    }
    @include laptop {
      transform: translate(-50%, 190%);
    }
  }

  &__signup-message {
    width: 90vw;
    max-height: fit-content;
    @include tablet {
      width: 65vw;
    }
    @include desktop {
      width: 35vw;
      min-width: 550px;
    }
    @include desktop-xl {
      min-width: 650px;
    }
  }
  &__message {
    &--title {
      @include title;
      padding: 0;
      text-shadow: 0px 2px 10px rgba(0, 0, 0, 0.65);
      font-size: 2rem;
      @include desktop {
        font-size: 2.25rem !important;
      }
    }
    &--content {
      @include description;
      padding: 0 !important;
      font-size: 1.25rem !important;
      margin: $space-xs 0;
    }
  }
  &__message-content {
    background-color: #798183;
    padding: $space-l;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  &__close {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    font-size: 1em;
    appearance: none;
    border: none;
    cursor: pointer;
    filter: brightness(0.15);
    transform: scale(0.65);

    @include desktop {
      top: 1rem;
      right: 1rem;
      transform: scale(1);
    }
  }

  &__confirm {
    margin-top: $space-xs;
    background-image: url("~assets/images/header/bg-button.png");
    background-size: cover;
    background-repeat: no-repeat;
    padding: $space-s $space-m;
    user-select: none;
    width: max-content;
    cursor: pointer;
    outline: none;
    text-transform: uppercase;
    border: none;
    font-weight: bold;
    white-space: nowrap;
    background-color: transparent;
    width: 120px;
    font-size: 1.125rem;
    @include desktop {
      font-size: 1.5rem;
      width: unset;
    }
  }

  .react-datepicker-wrapper {
    display: block;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      pointer-events: none;
      top: 50%;
      right: 10px;
      transform: translateY(-50%) rotate(-45deg);
      width: 8px;
      height: 8px;
      border-left: 2px solid #444;
      border-bottom: 2px solid #444;
      border-radius: 1px;
      z-index: 1;
    }
  }

  .react-datepicker__day--keyboard-selected {
    background-color: $color-p-yellow;
  }
}
