@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-introduce {
  position: relative;
  height: unset;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @include desktop-l {
    height: 100vh;
  }

  h2 {
    display: none;
  }
  video {
    max-height: 100%;
    width: 100%;
    height: 100vh;
    object-fit: cover;
    // @include desktop {
    //   height: unset;
    // }
  }
  &__logo {
    width: 70vw;
    max-width: 500px;
    img {
      max-width: 100%;
    }
  }
  &__download {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    font-size: 1.5rem;
    font-weight: bold;
    background-color: $color-p-yellow;
    text-transform: uppercase;
    text-align: center;
    padding: $space-s $space-m;
    @include desktop {
      height: 10vh;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 2rem;
      padding: 0 15vw;
    }
    figure {
      margin-top: 1rem;
      img {
        display: inline-block;
        width: 140px;
        margin-right: 0.5rem;
        cursor: pointer;
        @include desktop {
          width: unset;
          height: 50px;
          max-width: unset;
        }
      }
    }
  }
}
