@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-news-card {
  display: flex;
  flex-flow: column;
  position: relative;
  width: 286px;
  color: #ffffff;
  background-color: #000000;
  margin-bottom: $space-m;
  padding: $space-xs;
  font-size: 0.75rem;
  @include tablet {
    width: 331px;
  }

  &__image {
    width: 100%;
    z-index: 0;
  }

  &__image-container {
    position: relative;
    width: 100%;
    height: fit-content;
  }

  &__date {
    margin-top: $space-xs;
  }

  &__description {
    margin-top: $space-s;
  }

  &__facebook {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 3;
  }

  &__ribbon {
    position: absolute;
    top: 16px;
    right: -13px;
    z-index: 3;
  }

  &__link {
    margin-top: $space-xs;
    width: fit-content;
  }

  &__read-post {
    padding: 0.6rem 0.8rem;
    font-size: 0.65rem;
  }
}
