@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-founder-pack {
  position: relative;
  min-height: 100vh;
  padding: $space-m $space-l;
  text-align: center;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include tablet {
    min-height: unset;
  }

  @include desktop {
    padding: $space-xl;
    height: 100vh;
  }

  &__title {
    position: relative;
    @include title;
    padding: $space-m 0;
    @include desktop {
      padding: $space-s;
    }
  }

  &__description {
    position: relative;
    @include description;
    @include desktop {
      width: 80vw;
      max-width: 1024px;
    }
  }

  figure {
    .img-desktop {
      display: none;
      max-width: 50vw;

      @include desktop {
        display: block;
        margin: auto;
      }
    }
    .img-mobile {
      margin-top: $space-m;
      @include desktop {
        display: none;
      }
    }
    @include desktop {
      width: 75vw;
      max-width: 1300px;
      margin: auto;
    }

    @include desktop-2k {
      width: 75vw;
      max-width: unset;

      img {
        width: 100%;
      }
    }
  }

  &__btnDownload {
    @include desktop {
      padding: 1.5rem 2rem;
    }
  }
}
