@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-header {
  position: fixed;
  left: 0;
  width: 100%;
  height: 70px;
  background-color: #18141385;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 $space-m;
  z-index: 98;

  @include desktop {
    height: 10vh;
    max-height: 90px;
    background-color: black;
    padding: 0 $space-xl;
  }

  &__menu {
    min-width: 60px;
    @include desktop {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__logo {
    img {
      cursor: pointer;
      max-width: 40vw;
      @include desktop-xl {
        height: 70px;
      }
    }
  }

  &__btnSignup {
    font-weight: 700;
    background-image: url("~assets/images/header/bg-button.png");
    background-size: cover;
    background-repeat: no-repeat;
    padding: $space-s $space-m;
    font-size: 0.89rem;
    cursor: pointer;
    user-select: none;
    width: max-content;

    &.slider {
      margin: $space-l;
    }

    @include desktop {
      padding: var(--space-xs) var(--space-m);
    }
  }
}

.com-side-menu {
  position: fixed;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #262326;
  z-index: 99;
  transform: translateX(100%);

  @include desktop {
    width: 35%;
    min-width: 400px;
  }
  &__header {
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #181413;
    padding: 0 $space-m;
    color: black;

    @include desktop {
      height: 10vh;
      max-height: 90px;
      padding: 0 $space-xl 0 $space-l;
    }
    &--close {
      width: 1.5rem;
      cursor: pointer;
    }
  }

  &__content {
    color: $color-p-yellow;
    &--ul {
      display: flex;
      flex-direction: column;
      padding: 0 $space-l;
      li {
        position: relative;
        margin: 0.5rem 0;
      }

      a {
        font-size: 1.5rem;
        font-weight: 700;
        text-transform: uppercase;
        cursor: pointer;
        color: #a28027;
        transition: 0.3s all ease;

        &:hover {
          color: #e6b431;
        }
      }
    }

    &--logo {
      display: flex;
      align-items: center;
      padding: 0 $space-l;

      .text {
        font-size: 1rem;
        color: white;
        font-weight: bold;
        margin-right: $space-l;
      }
      img {
        min-height: 38px;
        cursor: pointer;
      }
    }
  }

  &__footer {
    color: white;
    font-size: 0.9rem;
    text-align: center;
    padding: $space-m;

    @include desktop {
      padding: 0;
      padding-bottom: $space-s;
    }

    li {
      position: relative;
      white-space: nowrap;
      margin-right: 1.5rem;
      cursor: pointer;
      width: fit-content;
      display: inline-block;
      line-height: 2;

      &:last-child::after {
        display: none;
      }

      &::after {
        content: " ";
        width: 4px;
        height: 4px;
        background-color: white;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 100%;
        transform: translate(0.5rem, -50%);
      }
    }
  }
}
