@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-feature {
  min-height: 100vh;
  padding: $space-m $space-l;
  padding-top: 0;
  text-align: center;

  @include desktop {
    padding: $space-m $space-xl;
    padding-top: 0;

    min-height: unset;
  }

  &__title {
    @include title;
  }
  &__description {
    @include description;
    color: black;
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;

    @include desktop {
      flex-direction: row;
    }

    @include desktop-xl {
      padding: 0 $space-xxl;
    }
  }
  &__card {
    // margin: $space-m;
    &--img {
      height: 45vh;
      object-fit: contain;
      @include laptop {
        height: 40vh;
      }
    }
    &--title {
      font-size: 1.65rem;
      font-weight: 800;
      text-transform: uppercase;
      margin: $space-s 0;
    }
    &--content {
      @include description;
      padding: 0;
      color: black;
    }
  }
}
