@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-slider-mobile {
  .carousel {
    &__wrapper {
      overflow: hidden;
      width: 100%;
    }

    &__container {
      display: flex;
      will-change: transform;
    }

    &__slide-container {
      position: relative;
      flex: 0 0 auto;
      width: 100%;
      transition: 0.3s all ease;
    }
  }
  .carousel-draggable {
    cursor: grab;
  }

  .carousel-dragging {
    cursor: grabbing;
  }

  .carousel-dots-container {
    .carousel-dot {
      cursor: pointer;
      width: 12px;
      height: 12px;
      background: transparent;
      border: 2px solid $color-p-yellow;
      border-radius: 50%;
      margin: 4px;

      &.is-selected {
        background: $color-p-yellow;
      }
    }
  }

  .carousel-buttons-container {
    .carousel-button {
      position: absolute;
      top: 40%;
      width: 50px;
      height: 50px;
      background: transparent;
      border: 0;
      outline: none;
      cursor: pointer;
      filter: brightness(300%);
      transition: 0.3s all ease;
      transform: scale(0.65);

      &:first-child {
        left: 0;
      }

      &:last-child {
        right: 0;
      }
    }

    .is-disable {
      filter: brightness(70%);
      cursor: not-allowed;
    }
  }
}
