@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-art {
  position: relative;
  min-height: 100vh;
  text-align: center;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: $space-m;

  &__title {
    @include title;
  }

  &__description {
    align-self: center;
    @include description;
    @include desktop {
      max-width: 700px;
    }
    @include desktop-2k {
      max-width: 60vw;
    }
  }

  &__wrapper--desktop {
    display: none;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: $space-xs $space-m;
    max-width: 60vw;
    margin: auto;

    @include desktop {
      display: grid;
    }
    @include desktop-2k {
      max-width: 60vw;
    }
    @include laptop {
      max-width: 65vw;
    }
  }

  &__card {
    position: relative;
    padding: 0 var(--space-m);
    img {
      height: 25vh;
      object-fit: contain;
      object-position: top;
      padding: $space-s;

      @include desktop {
        height: 50vh;
      }

      &.bg-mobile {
        @include desktop {
          display: none;
        }
      }
    }
    .download {
      position: absolute;
      width: 50px;
      height: 50px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: 0.2s all ease;
    }
  }

  &__card--desk {
    position: relative;

    .image {
      width: 100%;
      transition: 0.2s all ease;
    }

    .download {
      position: absolute;
      width: 35px;
      top: 40%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: 0.2s all ease;
      cursor: pointer;
      opacity: 0;
    }

    &:hover {
      .image {
        filter: brightness(50%);
      }

      .download {
        transition-delay: 0.15s;
        transform: translate(-50%, -50%) translateY(40%);
        opacity: 1;
      }
    }
  }
}

.carousel-art {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  position: relative;

  @include desktop {
    display: none;
  }

  .carousel {
    &__wrapper {
      overflow: hidden;
      width: 100%;
    }

    &__container {
      display: flex;
      will-change: transform;
    }

    &__slide-container {
      position: relative;
      flex: 0 0 auto;
      width: 100%;
      transition: 0.3s all ease;
    }
  }

  .carousel-dots-container {
    .carousel-dot {
      cursor: pointer;
      width: 12px;
      height: 12px;
      background: transparent;
      border: 2px solid $color-p-yellow;
      border-radius: 50%;
      margin: 4px;

      &.is-selected {
        background: $color-p-yellow;
      }
    }
  }

  .carousel-buttons-container {
    .carousel-button {
      position: absolute;
      top: 40%;
      width: 50px;
      height: 50px;
      background: transparent;
      border: 0;
      outline: none;
      cursor: pointer;
      filter: brightness(300%);
      transition: 0.3s all ease;
      transform: scale(0.65);

      &:first-child {
        left: 0;
      }

      &:last-child {
        right: 0;
      }
    }

    .is-disable {
      filter: brightness(70%);
      cursor: not-allowed;
    }
  }
}
