// this define the global style AND it should be inlcude only in index.scss
@import url("https://fonts.googleapis.com/css?family=Montserrat:400,500,700,900&display=swap");
@import "assets/styles/variable.scss";
@import "assets/styles/mixin.scss";
@import "./spacing.scss";

// reset, default
html,
body {
  margin: 0;
  color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  overflow-x: hidden;
  @include tablet {
    font-size: 15px;
  }

  @include desktop {
    font-size: 20px;
  }

  // special laptop
  @include laptop {
    font-size: 16px;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}
.grecaptcha-badge {
  display: none !important;
}
ul {
  padding: 0;
  li {
    list-style-type: none;
  }
}
a {
  text-decoration: none;
  color: unset;
}
// custom
.mg-l05 {
  margin-left: 0.5rem;
}
.mg-r05 {
  margin-right: 0.5rem;
}
.mg-r1 {
  margin-right: 1rem;
}
.mg-r15 {
  margin-right: 1.5rem;
}
.mg-b1 {
  margin-bottom: 1rem;
}
.w-unset {
  width: unset !important;
}
.absolute-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.index-0 {
  z-index: 0 !important;
}
.bg-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-position: top;
  z-index: -1;
  object-fit: cover;
}
.mobile {
  display: block;
  @include desktop {
    display: none;
  }
}

.tablet {
  display: block;
  @include desktop {
    display: none;
  }
}

.desktop {
  display: none;
  @include desktop {
    display: block;
  }
}
